<template>
  <v-page name="cartao">
    <div class="d-flex justify-center">
      <v-card
        class="pa-3"
        elevation="2"
        style="width: 600px"
      >
        <h2>Dados do carto</h2>
        <v-stripe-card
          v-model="source"
          api-key="pk_live_51HbFJ0GkUXTqQoyNv5tRV8E5meLvdmVwnMDYsz3iqwo2qOTyhMnsSlttd00sLo2KjWzEy3xeIyIaeBEUybpOAQxQ00om469Qzt"
          :customStyle="{currency: 'BRL', adress_country: 'BR'}"
        ></v-stripe-card>
        <v-btn text>Cancelar</v-btn>
        <v-btn
          color="green"
          :disabled="source === null"
          @click="sendPaymentMethod()"
        >Pagar</v-btn>
      </v-card>
    </div>
    <v-dialog
      v-model="successDialog"
      width="500"
    >
      <v-card>
