          Pagamento realizado
        </v-card-title>

        <v-card-text>
          Seu pagamento foi realizado, obrigado.
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="successDialog = false"
          >
            Ok
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="errorDialog"
      width="500"
    >
      <v-card>
        <v-card-title class="headline grey lighten-2">
          Pagamento realizado
        </v-card-title>

        <v-card-text class='pt-4'>
          Ocorreu um erro durante o pagamento, seu pagamento no foi realizado, verifique com o .
        </v-card-text>
