<script>
import axios from 'axios';


export default {
  data () {
    return {
      source: null,
      successDialog: false,
      errorDialog: false,
      paymentClicked: 0,
    }
  },
  methods: {
    typingStarted () {
      console.log('Selecionado')
      this.typing = true
    },
    pagar () {
      console.log(this.source)
    },
    cancelar () {
      this.source = null
    },
    sendPaymentMethod () {
      if (this.source !== null && this.paymentClicked === 0) {
        this.paymentClicked = 1
        axios({
          method: 'POST',
          url: 'https://serene-hamlet-40798.herokuapp.com/pay',
          // url: 'http://localhost:34939/pay',
          data: {
            token: this.source.id,
            amount: 100,
            client_email: sessionStorage.getItem('current-user'),
            plan: 'inicial'
          }
        }).then((response) => {
          console.log(response)
          switch (response.status) {
            case 202: {
              // alert('Pagamento realizado com sucesso')
              this.successDialog = true
              break;
            }
            case 500: {
              // alert('Server side error, desenvolvedores alertados, sua compra no foi realizada.')
              this.errorDialog = true
              break
            }
            case 400: {
              alert('400')
              break
            }


            // default:
            //   break;
          }
        })
      } else {
        alert("Insira os dados do carto primeiro ")
      }
    }
  }
}
</script>