<template>
  <v-page style="heigth: 100%">
    <v-row class="fill-height">
      <v-col>
        <v-sheet height="64">
          <v-toolbar flat>
            <v-btn
              outlined
              class="mr-4"
              color="grey darken-2"
              @click="setToday"
            >
              Hoje
            </v-btn>
            <v-btn
              fab
              text
              small
              color="grey darken-2"
              @click="prev"
            >
              <v-icon small>
                mdi-chevron-left
              </v-icon>
            </v-btn>
            <v-btn
              fab
              text
              small
              color="grey darken-2"
              @click="next"
            >
              <v-icon small>
                mdi-chevron-right
              </v-icon>
            </v-btn>
            <v-toolbar-title v-if="$refs.calendar">
              {{ $refs.calendar.title }}
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <!-- <v-btn
              class="mr-2"
              outlined
              color="grey darken-2"
              @click="openEventForm"
            >Novo agendamento</v-btn> -->
            <v-menu
              bottom
              right
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  outlined
                  color="grey darken-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  <span>{{ typesPt[type] }}</span>
                  <v-icon right>
                    mdi-menu-down
                  </v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-item @click="type = 'day'">
                  <v-list-item-title>Dia</v-list-item-title>
                </v-list-item>
                <v-list-item @click="type = 'week'">
                  <v-list-item-title>Semana</v-list-item-title>
                </v-list-item>
                <v-list-item @click="type = 'month'">
                  <v-list-item-title>Ms</v-list-item-title>
                </v-list-item>
                <v-list-item @click="type = '4day'">
                  <v-list-item-title>4 dias</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </v-toolbar>
        </v-sheet>
        <v-sheet
          height="600"
          @mousemove="hourInfoMouseFollow"
        >
          <v-calendar
            ref="calendar"
            v-model="focus"
            color="primary"
            locale="pt"
            :events="events"
            :event-color="getEventColor"
            :type="type"
            @click:event="showEvent"
            @click:more="viewDay"
            @click:date="viewDay"
            @click:time="openEventForm"
            @mousemove:time="getCalendarDayTime"
            @change="getEvents"
          >
            <template v-slot:day-body="{ date, week }">
              <v-chip
                id="DateChip"
                class="v-current-time"
                :class="{ first: date === week[0].date }"
                :style="{ width: 100 + 'px'}"
              >
                Hora: {{ currentTime }}
              </v-chip>
            </template>

          </v-calendar>
          <!-- Menu poup para edio do horrio -->
          <v-menu
            v-model="selectedOpen"
            :close-on-content-click="false"
            :activator="selectedElement"
            offset-x
          >
            <v-card
              color="grey lighten-4"
              min-width="350px"
              flat
            >
              <v-toolbar
                :color="selectedEvent.color"
                dark
              >
                <v-btn
                  icon
                  @click="editEvent = !editEvent"
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn
                  icon
                  @click="deleteEvent(selectedEvent.id)"
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </v-toolbar>
              <v-card-text>
                <p v-if="editEvent == false">{{ selectedEvent.details }}</p>
                <v-textarea
                  v-else
                  v-model="selectedEvent.details"
                ></v-textarea>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  text
                  color="secondary"
                  @click="selectedOpen = false; editEvent = false"
                >
                  Cancel
                </v-btn>

                <v-btn
                  v-if="editEvent"
                  color="secondary"
                  text
                  @click="saveEvent(selectedEvent.id)"
                >
                  Save
                </v-btn>
                <div v-else></div>
              </v-card-actions>
            </v-card>
          </v-menu>

        </v-sheet>
      </v-col>
    </v-row>

    <v-row justify="center">
      <v-dialog
        v-model="dialog"
        persistent
        max-width="600px"
      >
        <v-card>
          <v-card-title>
            <span class="headline">Agendamento</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-autocomplete
                    :items="['1 Consulta', '1 Retorno', '2 Consulta', '2 Retorno', '3 Consulta', '3 Retorno',]"
                    label="Tipo de atendimento *"
                    v-model="atendimento"
                  ></v-autocomplete>
                </v-col>
                <v-col cols="6">
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="startTime"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="startTime"
                        label="Horrio de inicio *"
                        prepend-icon="mdi-clock-time-four-outline"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      format="24hr"
                      scrollable
                      color="green lighten-1"
                      v-model="startTime"
                      @click:minute="$refs.menu.save(startTime)"
                      min="7:00"
                      max="21:00"
                    ></v-time-picker>
                  </v-menu>
                </v-col>
                <v-col cols="6">
                  <v-menu
                    ref="menu2"
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="endTime"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="endTime"
                        label="Horrio de termino *"
                        prepend-icon="mdi-clock-time-four-outline"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      format="24hr"
                      scrollable
                      color="green lighten-1"
                      v-model="endTime"
                      @click:minute="$refs.menu2.save(endTime)"
                      min="7:00"
                      max="21:00"
                    ></v-time-picker>
                  </v-menu>
                </v-col>

                <v-col cols="3">
                  <v-checkbox
                    v-model="newPatient"
                    @click="patient = ''"
                    label="Paciente novo"
                  ></v-checkbox>
                </v-col>

                <v-col cols="9">
                  <v-autocomplete
                    v-if="!newPatient"
                    v-model="patient"
                    label="Paciente *"
                    required="true"
                    :items="patients"
                  ></v-autocomplete>
                  <v-text-field
                    label="Paciente *"
                    v-else
                    v-model="patient"
                  ></v-text-field>

                </v-col>

                <v-col>
                  <v-textarea
                    label="Descrio"
                    v-model="description"
                  >
                  </v-textarea>
                </v-col>
              </v-row>
            </v-container>
            <small>*indica campo obrigatrio</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="blue darken-1"
              text
              @click="clearInfo"
            >
              Close
            </v-btn>
            <v-btn
              color="blue darken-1"
              text
              @click="addEvent"
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>

  </v-page>
</template>

<script>
import firebase from "firebase";

export default {
  name: 'Agenda',
  data () {
    return {
      menu: null,
      menu2: null,
      atendimento: null,
      startTime: null,
      endTime: null,
      currentTime: '',
      newPatient: false,
      description: null,
      focus: '',
      patient: '',
      patients: [],
      selectedTime: null,
      dialog: false,
      type: 'week',
      typesPt: {
        month: 'Ms',
        week: 'Semana',
        day: 'Dia',
        '4day': '4 Dias'
      },
      typeToLabel: {
        month: 'Month',
        week: 'Week',
        day: 'Day',
        '4day': '4 Days',
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      editEvent: false,
      events: [],
      colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
      names: ['Meeting', 'Holiday', 'PTO', 'Travel', 'Event', 'Birthday', 'Conference', 'Party'],
    }
  },
  methods: {
    async getPatients () {
      this.patients = await firebase.firestore().collection("users")
        .doc(sessionStorage.getItem('current-user'))
        .collection("pacientes")
        .get()
        .then(async (snapshot) => {
          return await snapshot.docs.map((doc) => {
            return {
              text: doc.data().nome,
              value: doc.id
            }
          })
        })
    },
    clearInfo () {
      this.dialog = false
      this.patient = ''
      this.newPatient = false
      this.startTime = null
      this.endTime = null
      this.description = null
      this.selectedTime = null
    },
    hourInfoMouseFollow (e) {
      try {
        var chip = document.getElementById('DateChip')
        if ((this.type === "week" || this.type === "day") && e.target.className == "v-calendar-daily__day-interval") {
          chip.style.display = "block"
          chip.style.left = (e.clientX - 300) + 'px'
          chip.style.top = e.layerY + 'px'
        } else {
          chip.style.display = "none"
        }
      } catch (err) {
        err
      }
    },
    viewDay ({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor (event) {
      return event.color
    },
    async deleteEvent (eventId) {
      await firebase.
        firestore()
        .collection("users")
        .doc(sessionStorage.getItem('current-user'))
        .collection("agenda")
        .doc(eventId)
        .delete()
      this.type = 'day'
      setTimeout(() => {
        this.type = 'week'
      }, 1);
      this.selectedOpen = false
    },
    async saveEvent (eventId) {
      await firebase.
        firestore()
        .collection("users")
        .doc(sessionStorage.getItem('current-user'))
        .collection("agenda")
        .doc(eventId)
        .set(this.selectedEvent).then(() => { this.editEvent = false })
    },
    async getEvents () {
      var events = []
      var eventList = await firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem('current-user'))
        .collection("agenda").get()
        .then((snapshot) =>
          snapshot.docs.map((doc) => {
            let data = doc.data()
            data.id = doc.id
            return data
          })
        )
      eventList.forEach((event) => events.push({
        name: event.name,
        start: new Date(event.start),
        end: new Date(event.end),
        details: event.details,
        color: event.color,
        timed: event.timed,
        id: event.id,
      }))
      this.events = events
    },
    openEventForm (time) {
      this.dialog = true
      this.selectedTime = time
      this.startTime = time.time
    },
    addEvent () {
      if (this.startTime !== null && this.endTime !== null && this.patient !== '' && this.atendimento) {
        this.dialog = false
        firebase.firestore()
          .collection("users")
          .doc(sessionStorage.getItem('current-user'))
          .collection("agenda")
          .add({
            name: this.atendimento,
            start: `${this.selectedTime.date} ${this.startTime}:00`,
            details: this.description,
            end: `${this.selectedTime.date} ${this.endTime}:00`,
            color: this.atendimento === '1 Consulta' ? '#FFB400' : 'blue',
            timed: true,
          })
        this.$refs.calendar.checkChange()
        this.$refs.calendar.$forceUpdate()
        this.type = "day"
        setTimeout(() => {
          this.type = "week"
        }, 30);
        this.clearInfo()
      } else {
        alert("Preencha os campos antes de continuar")
      }

    },
    // async deleteEvent (ev) {
    //   this.selectedOpen = false
    //   this.getEvents()
    // },
    getCalendarDayTime (time) {
      this.currentTime = time.time
    },
    setToday () {
      this.focus = ''
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    showEvent ({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        setTimeout(() => {
          this.selectedOpen = true
        }, 10)
      }

      if (this.selectedOpen) {
        this.selectedOpen = false
        setTimeout(open, 10)
      } else {
        open()
      }

      nativeEvent.stopPropagation()
    },
    rnd (a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a
    },
  },
  mounted () {
    this.$refs.calendar.checkChange()
    this.getPatients()
  },
}
</script>

<style scoped lang="scss">
.v-event-draggable {
  padding-left: 6px;
}

.v-event-timed {
  user-select: none;
  -webkit-user-select: none;
}

.v-event-drag-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 4px;
  height: 4px;
  cursor: ns-resize;

  &::after {
    display: none;
    position: absolute;
    left: 50%;
    height: 4px;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    width: 16px;
    margin-left: -8px;
    opacity: 0.8;
    content: "";
  }

  &:hover::after {
    display: block;
  }
}

.v-current-time {
  height: 2px;
  background-color: #ea4335;
  position: absolute;
  left: -1px;
  right: 0;
  pointer-events: none;

  &.first::before {
    content: "";
    position: absolute;
    background-color: #ea4335;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-top: -5px;
    margin-left: -6.5px;
  }
}
</style>