<template>
  <div class="d-flex flex-column justify-center">
    <v-card
      class="d-inline-block mr-3"
      width="100%"
      elevation="3"
      color="grey lighten-2"
    >
      <v-container>
        <v-card-actions>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" class="mx-2">
                <v-icon color="#614021">{{ icons.mdiEye }}</v-icon>
              </v-btn>
            </template>
            <span>Visualizar Dieta</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" class="mx-2">
                <v-icon color="#614021">{{ icons.mdiPrinter }}</v-icon>
              </v-btn>
            </template>
            <span>Imprimir Dieta</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="#c5d13f"
                v-bind="attrs"
                v-on="on"
                @click="createDocWithJSPDF"
              >
                <v-icon color="#614021">{{ icons.mdiDownload }}</v-icon>
              </v-btn>
            </template>
            <span>Baixar Dieta</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="#c5d13f" v-bind="attrs" v-on="on" class="mx-4">
                <v-icon color="#614021">{{ icons.mdiContentSave }}</v-icon>
              </v-btn>
            </template>
            <span>Salvar Dieta</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="#c5d13f" v-bind="attrs" v-on="on">
                <v-icon color="#614021">{{ icons.mdiEmailSendOutline }}</v-icon>
              </v-btn>
            </template>
            <span>Enviar Dieta ao Paciente</span>
          </v-tooltip>
        </v-card-actions>

        <v-divider></v-divider>

        <v-row class="d-flex justify-center">
          <v-col cols="12" md="8">
            <v-tooltip top>
              <template v-slot:activator="{ on, attrs }">
                <v-card
                  v-bind="attrs"
                  v-on="on"
                  elevation="0"
                  color="grey lighten-2"
                >
                  <v-autocomplete
                    label="Nome do Paciente"
                    single-line
                    solo
                    class="mt-2 mx-3"
                    v-model="pacienteDocID"
                    :items="pacientes"
                  ></v-autocomplete>
                </v-card>
              </template>
              <span
                >Selecione o nome de um paciente cadastrado para anexar a dieta
                </span
              >
            </v-tooltip>
          </v-col>
          <v-col cols="12" md="4">
            <v-btn class="mt-3">Ver histrico de atendimento</v-btn>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center">
          <v-chip color="#c5d13f" class="d-flex align-center">
            <v-row class="px-2 black--text">
              <p class="px-2 mt-4">
                KCAL: <strong>{{ dietaStatus.KCAL.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                CHO: <strong>{{ dietaStatus.CHO.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                LIP: <strong>{{ dietaStatus.LIP.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                COL: <strong>{{ dietaStatus.COL.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                SOD: <strong>{{ dietaStatus.SOD.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                FIB: <strong>{{ dietaStatus.FIB.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                PTN: <strong>{{ dietaStatus.PTN.toFixed(2) }}</strong>
              </p>
            </v-row>
          </v-chip>
          <v-dialog v-model="dialog" width="90%">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                fab
                color="#c5d13f"
                class="mx-2"
                x-small
                v-bind="attrs"
                v-on="on"
              >
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }"
                    ><v-icon color="#614021" v-bind="attrs" v-on="on">{{
                      icons.mdiDotsHorizontal
                    }}</v-icon>
                  </template>
                  <span>Mais informaes nutricionais</span>
                </v-tooltip>
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="h6"
                  >Informaes nutricionais detalhadas da dieta</span
                >
              </v-card-title>
              <v-card-text>
                <v-container> 
                  <v-simple-table>
                    <template v-slot:default>
                      <tbody>
                        <tr v-for="nutrient in allNutrients" :key="nutrient.codigo">
                          <td>{{ nutrient.nome }}:</td>
                          <td> 
                            <strong>{{
                              dietaStatus[nutrient.codigo].toFixed(2)
                            }}</strong>
                          </td> 
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-container>
              </v-card-text>
              <v-card-actions class="d-flex justify-center">
                <v-btn color="#c5d13f" text @click="dialog = false">
                  Fechar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-container>
    </v-card>

    <DietaForm></DietaForm>

    <template>
      <v-snackbar
        timeout="-1"
        elevation="0"
        bottom
        right
        multi-line
        v-model="snack"
        color="rgba(0, 0, 0, 0)"
      >
        <div>
          <v-row class="d-flex justify-center align-center">
            <v-chip color="#c5d13f" class="d-flex align-center">
              <v-row class="px-2 black--text">
                <p class="px-2 mt-4">
                  KCAL: <strong>{{ dietaStatus.KCAL.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  CHO: <strong>{{ dietaStatus.CHO.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  LIP: <strong>{{ dietaStatus.LIP.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  COL: <strong>{{ dietaStatus.COL.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  SOD: <strong>{{ dietaStatus.SOD.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  FIB: <strong>{{ dietaStatus.FIB.toFixed(2) }}</strong>
                </p>
                <p class="px-2 mt-4">
                  PTN: <strong>{{ dietaStatus.PTN.toFixed(2) }}</strong>
                </p>
              </v-row>
            </v-chip>
            <v-btn
              fab
              color="#c5d13f"
              class="mx-2 my-1"
              x-small
              @click="dialog = true"
            >
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }"
                  ><v-icon color="#614021" v-bind="attrs" v-on="on">{{
                    icons.mdiDotsHorizontal
                  }}</v-icon>
                </template>
                <span>Mais informaes nutricionais</span>
              </v-tooltip>
            </v-btn>
          </v-row>
        </div>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
// import firebase from "firebase";
import {
  mdiCalendar,
  mdiContentSave,
  mdiPrinter,
  mdiEye,
  mdiEmailSendOutline,
  mdiDownload,
  mdiMagnify,
  mdiDotsHorizontal,
  mdiInformationOutline,
  mdiClose,
} from "@mdi/js";

// const docx = require("docx");
// import { saveAs } from "file-saver";

import jspdf from 'jspdf'
import 'jspdf-autotable'
// import imagesloaded from 'imagesloaded'


// components imports
import DietaForm from "../components/home/dieta/DietaForm";

export default {
  components: { DietaForm },
  data: function () {
    return {
      snack: true,
      table: false,
      dialog: false,
      snackbar: true,
      allNutrients: [
        { codigo: "KCAL", nome: "Calorias" }, //NaN
        { codigo: "PTN", nome: "Proteina" },
        { codigo: "LIP", nome: "Lipideos" },
        { codigo: "COL", nome: "Colesterol" }, 
        { codigo: "CHO", nome: "Carboidrato" },
        { codigo: "FIB", nome: "Fibra alimentar" },
        { codigo: "fibra_soluvel", nome: "Fibra solvel" },
        { codigo: "fibra_insoluvel", nome: "Fibra insolvel" },
        { codigo: "cinzas", nome: "Cinzas" },
        { codigo: "calcio", nome: "Clcio" },
        { codigo: "magnesio", nome: "Magnsio" },
        { codigo: "manganes", nome: "Mangans" },
        { codigo: "fosforo", nome: "Fsforo" },
        { codigo: "ferro", nome: "Ferro" },
        { codigo: "SOD", nome: "Sdio" },
        { codigo: "potassio", nome: "Potassio" },
        { codigo: "cobre", nome: "Cobre" },
        { codigo: "zinco", nome: "Zinco" },
        { codigo: "retinol", nome: "Retinol" },
        { codigo: "re", nome: "RE" },
        { codigo: "era", nome: "ERA" },
        { codigo: "iodo", nome: "Iodo" },
        { codigo: "selenio", nome: "Selnio" },
        { codigo: "acido_folico", nome: "cido flico" },
        { codigo: "acido_pantotenico", nome: "cido pantotnico" },
        { codigo: "tiamina", nome: "Tiamina" },
        { codigo: "riboflavina", nome: "Riboflavina" },
        { codigo: "piridoxina", nome: "Piridoxina" },
        { codigo: "niacina", nome: "Niacina" },
        { codigo: "vitamina_c", nome: "Vitamina C" },
        { codigo: "vitamina_d", nome: "Vitamina D" },
        { codigo: "vitamina_e", nome: "Vitamina E" },
        { codigo: "vitamina_b12", nome: "Vitamina B12" },
        { codigo: "saturados", nome: "Saturados" },
        { codigo: "monoinsaturados", nome: "Mono-insaturados" },
        { codigo: "poliinsaturados", nome: "Poli-insaturados" },
        { codigo: "gordura_trans", nome: "Gordura trans" },
        { codigo: "triptofano", nome: "Triptofano" },
        { codigo: "treonina", nome: "Treonina" },
        { codigo: "isoleucina", nome: "Isoleucina" },
        { codigo: "leucina", nome: "Leucina" },
        { codigo: "lisina", nome: "Lisina" },
        { codigo: "metionina", nome: "Metionina" },
        { codigo: "cistina", nome: "Cistina" },
        { codigo: "fenilalanina", nome: "Fenilalanina" },
        { codigo: "tirosina", nome: "Tirosina" },
        { codigo: "valina", nome: "Valina" },
        { codigo: "arginina", nome: "Arginina" },
        { codigo: "histidina", nome: "Histidina" },
        { codigo: "alanina", nome: "Alanina" },
        { codigo: "acido_aspartico", nome: "cido aspartico" },
        { codigo: "acido_glutamico", nome: "cido glutmico" },
        { codigo: "glicina", nome: "Glcina" },
        { codigo: "prolina", nome: "Prolina" },
        { codigo: "serina", nome: "Serina" },
        { codigo: "umidade", nome: "Umidade" },
      ],
      pacienteDocID: "",
      icons: {
        mdiCalendar,
        mdiContentSave,
        mdiPrinter,
        mdiEye,
        mdiEmailSendOutline,
        mdiDownload,
        mdiMagnify,
        mdiDotsHorizontal,
        mdiInformationOutline,
        mdiClose,
      },
      preloadLogo: new Image(),
      preloadSignature: new Image()
    };
  },
  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    dietaStatus() {
      return this.$store.state.firestore.dietaStatus;
    },
    dietaRefeicoes() {
      return this.$store.state.firestore.dietaRefeicoes;
    },
    alimentos() {
      return this.$store.state.firestore.alimentos;
    },
  },
  methods: {
    createListWithNames() {
      var values = Object.values(this.pacientes);
      this.pacientes = values.map((valueItem) => {
        return {
          value: valueItem.id,
          text: valueItem.nome,
        };
      });
    },
    print() {
      console.log(
        this.dietaRefeicoes[0].alimentosList.map((alimento) => {
          return [
            this.alimentos.find((item) => item.value == alimento.alimentoId)
              .data.nome,
            alimento.quantidade.toString(),
            alimento.medida.toString(),
            alimento.gramas.toString(),
            alimento.KCAL.toString(),
          ];
        })
      );
    },
    async preloadImages () {
      this.preloadLogo.crossOrigin = 'anonymous'
      this.preloadSignature.crossOrigin = 'anonymous'
      this.preloadLogo.src = `https://cors-anywhere.herokuapp.com/${this.$store.state.firestore.userInfo.logo}`
      this.preloadSignature.src = `https://cors-anywhere.herokuapp.com/${this.$store.state.firestore.userInfo.signature}`
      this.preloadLogo.onload = function () { console.log('Logo carregada') }
      this.preloadSignature.onload = function () { console.log('Signature carregada') }

      console.log("natural:", this.preloadLogo.naturalWidth, this.preloadLogo.naturalHeight)
      console.log("natural:", this.preloadSignature.naturalWidth, this.preloadSignature.naturalHeight)
    },
    async createDocWithJSPDF () {
      if (this.dietaRefeicoes.length !== 0) {
        alert("Gerando PDF")
        // links
        // https://mrrio.github.io/jsPDF/examples/basic.html
        // http://www.rotisedapsales.com/snr/cloud_staging/website/jsPDF-master/docs/global.html#text
        // https://simonbengtsson.github.io/jsPDF-AutoTable/
        // https://www.npmjs.com/package/jspdf-autotable


        var doc = new jspdf('p')
        var date = new Date()
        var day = date.getDate()
        var month = date.getMonth() + 1
        var year = date.getFullYear()

        // Doc settings
        var fontSize = 12
        doc.setFontSize(fontSize)
        doc.setFont("Times")

        // The (900 / 262) division is used to keep the proportion of the original image
        // New Width * (900 / 262) will keep the height proportion

        var imgWidth = 40;
        var imgHeight = imgWidth / (900 / 262);
        var imgPos = (210 - imgWidth) / 2;
        var startXPos = 15;
        var textStartYPos = imgHeight * 4;

        var centeredText = function (text, y) {
          var textWidth =
            (doc.getStringUnitWidth(text) * doc.internal.getFontSize()) /
            doc.internal.scaleFactor;
          var textOffset = (doc.internal.pageSize.width - textWidth) / 2;
          doc.text(textOffset, y, text);
        };

        var createTexts = function (textList, centered = false, textStart = textStartYPos) {

          for (var i = 0; i < textList.length; i++) {
            if (centered) {
              centeredText(textList[i], textStart + (fontSize / 2) * (1 + i));
            } else {
              var lines = doc.setFont("Roboto").splitTextToSize(textList[i], 180)
              // console.log(lines)
              for (var j = 0; j < lines.length; j++) {
                doc.text(startXPos, textStart + ((fontSize / 2) * (1 + j)), lines[j])
              }
              textStart += (fontSize / 2) * (1 + lines.length)

            }
          }
          return textStart
        }

        var createTablesForRefeicoes = function (refeicoes, startPosY) {
          // #c5d13f <----- trocar a cor da tabela
          for (var i = 0; i < refeicoes.length; i++) {
            var texts = [
              `${refeicoes[i].nomeRefeicao} - ${refeicoes[i].horarioRefeicao}`,
              `OBS.: ${refeicoes[i].observacao}`
            ]
            var lastPos = createTexts(texts, false, startPosY)
            var body = refeicoes[i].alimentosList.map((alimento) =>
              [
                alimento.nome,
                alimento.quantidade.toString(),
                alimento.medida.toString(),
                alimento.gramas.toString(),
                alimento.calorias.toString(),
              ]
            )
            // console.log(lastPos)
            doc.autoTable({
              startY: ((lastPos / 2) * 0.70) + startPosY,
              startx: startXPos,
              head: [["Alimento", "Quantidade", "Medida", "Total (g)", "KCAL"]],
              body: body,
            });
            startPosY = doc.previousAutoTable.finalY + fontSize * (3 / 4);
          }
          return doc.previousAutoTable.finalY
        }

        console.log(this.$store.state.firestore.userInfo.logo)
        // Logo Clubedonutricionista
        if (this.$store.state.firestore.userInfo.useLogo === false) {
          console.log('op 1')
          doc.addImage({
            imageData: require("../assets/logoCompleto.png"),
            y: startXPos,
            x: imgPos,
            w: imgWidth,
            h: imgHeight
          })
        } else {

          // LOGO CLUBE
          imgWidth = 30
          imgHeight = imgWidth / (900 / 262)
          startXPos = 15

          doc.addImage({
            imageData: require("../assets/logoCompleto.png"),
            y: startXPos,
            x: 15,
            w: imgWidth,
            h: imgHeight
          })

          // var img = document.createElement("IMG")
          // img.setAttribute("src", `https://cors-anywhere.herokuapp.com/${this.$store.state.firestore.userInfo.logo}`)
          // // img.setAttribute("src", "https://cors-anywhere.herokuapp.com/" + await firebase
          // //   .storage()
          // //   .ref("logos")
          // //   .child(sessionStorage.getItem("current-user") + '_logo')
          // //   .getDownloadURL())
          // console.log(await firebase
          //   .storage()
          //   .ref("logos")
          //   .child(sessionStorage.getItem("current-user") + '_logo').getMetadata())
          // // x.clientWidth
          // // x.clientHeight

          imgWidth = 50
          var startYPos = 15 + (imgHeight * 2)
          imgHeight = imgWidth / (this.preloadSignature.naturalWidth / this.preloadSignature.naturalHeight)
          imgPos = (210 - imgWidth) / 2
          textStartYPos = imgHeight + (4 * (imgHeight / fontSize)) + startYPos

          doc.addImage({
            imageData: this.preloadSignature,
            y: startYPos,
            x: imgPos,
            w: imgWidth,
            h: imgHeight
          })

        }

        var texts = [
          `Plano alimentar de ${this.dietaStatus.KCAL.toFixed(2)} kcal`,
          `Data: ${day}/${month}/${year}`
        ]

        createTexts(texts);
        var endTablePosY = createTablesForRefeicoes(
          this.dietaRefeicoes,
          textStartYPos + texts.length * (fontSize * (3 / 4))
        );

        // doc.autoTable({
        //   startY: textStartYPos + ((texts.length) * (fontSize * (3 / 4))),
        //   startx: startXPos,
        //   head: [['Alimento', "Quantidade", "Medida", "Total (g)", "KCAL"]],
        //   body: [
        //     ['Alface', '12', 'Colher de sopa', '120', '12'],
        //     ['Alface', '12', 'prato cheio', '120', '12'],
        //     ['Alface', '12', 'nsei', '120', '12'],
        //     ['Alface', '12', 'nsei', '120', '12'],
        //     ['Alface', '12', 'nsei', '120', '12'],
        //   ]
        // })


        // USE SIGNATURE
        if (this.$store.state.firestore.userInfo.signature !== undefined) {

          imgWidth = 30
          startYPos = fontSize + (endTablePosY !== undefined ? endTablePosY + 16 : textStartYPos + (texts.length * (fontSize * (3 / 4))))
          imgHeight = imgWidth / (this.preloadLogo.naturalWidth / this.preloadLogo.naturalHeight)
          imgPos = (210 - imgWidth) / 2
          textStartYPos = imgHeight + (4 * (imgHeight / fontSize)) + startYPos

          doc.addImage({
            imageData: this.preloadSignature,
            y: startYPos,
            x: imgPos,
            w: imgWidth,
            h: imgHeight
          })

        } else {
          imgHeight = 0
        }




        createTexts([
          '____________________________',
          `Nutricionista ${this.$store.state.firestore.userInfo.name}`,
          'CRN: numero',
          `Data: ${day}/${month}/${year}`
        ],
          true,
          endTablePosY !== undefined ? endTablePosY + 16 + (imgHeight * 1.4) : textStartYPos + (texts.length * (fontSize * (3 / 4)))
        )

        doc.save("test.pdf");
      } else {
        alert("Preencha todos os campos da dieta antes de continuar");
      }
    },
  },
  // firestore() {
  //   return {
  //     documentsList: firebase
  //       .firestore()
  //       .collection("users")
  //       .doc(sessionStorage.getItem("current-user"))
  //       // .doc(firebase.auth().currentUser.email)
  //       .collection("pacientes"),
  //   };
  // },
  mounted() {
    if (this.$store.state.firestore.alimentos.length === 0) {
      this.$store.dispatch("firestore/getAlimentos");
    }
    if (this.$store.state.firestore.pacientes.length === 0) {
      this.$store.dispatch("firestore/getPacientes");
    }
    if (this.$store.state.firestore.receitas.length === 0) {
      this.$store.dispatch("firestore/getReceitas");
    }    
    setTimeout(() => {
      this.createListWithNames();
    }, 1000);
    this.preloadImages()
  },
};
</script>

<style>
</style>