<script>
import {
  mdiAccount,
  mdiWeb,
  mdiLock,
  mdiEmail,
  mdiEye,
  mdiEyeOff,
  mdiStore,
  mdiAlert,
  mdiCalendar,
  mdiPhone,
  mdiWhatsapp,
  mdiMapMarker,
  mdiCity,
  mdiFlagVariantOutline,
  mdiPiggyBank,
  mdiHandHeart,
  mdiBabyCarriage,
  mdiHumanMaleChild,
  mdiHospitalBuilding,
  mdiAccountVoice,
} from "@mdi/js";

export default {
  name: "cadastro",
  data: function () {
    return {
      pacienteNrMax: 1000,
      menuDataNascimento: false,
      pacienteInfos: {
        gestante: "false",
        sexo: "feminino",
        dataNascimento: null,
        telefone: "",
        whatsapp: "",
        nome: "",
        email: "",
        // senha: "",
        // endereco: "",
        // cidade: "",
        // estado: "",
        // profissao: "",
        convenio: "",
        // estadoCivil: null,
        // filhos: "",
        // idadeFilhos: "",
        // indicacao: "",
      },
      estadoCivilItems: [
        "-",
        "Solteira/Solteiro",
        "Casada/Casado",
        "Viva/Vivo",
        "Divorciada/Divorciado",
      ],
      icons: {
        mdiWeb,
        mdiLock,
        mdiEmail,
        mdiEye,
        mdiEyeOff,
        mdiStore,
        mdiAccount,
        mdiAlert,
        mdiCalendar,
        mdiPhone,
        mdiWhatsapp,
        mdiMapMarker,
        mdiCity,
        mdiFlagVariantOutline,
        mdiPiggyBank,
        mdiHospitalBuilding,
        mdiHandHeart,
        mdiBabyCarriage,
        mdiHumanMaleChild,
        mdiAccountVoice,
      },
      show: false,
      valid: true,
      rules: {
        requiredField: [(v) => !!v || "Campo obrigatrio"],
        // passwordLength: [(v) => v.length >= 8 || "Senha com mais que 8 letras"],
        emailRules: [
          (v) => !!v || "E-mail obrigatrio",
          (v) => /.+@.+/.test(v) || "E-mail invalido",
        ],
      },
    };
  },
  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    pacienteNr: function () {
      return this.pacientes.length;
    },
    pacienteRatio: function () {
      return (this.pacienteNr / this.pacienteNrMax) * 100;
    },
  },
  methods: {
    submitIniciar: function () {
      console.log(this.pacienteInfos)
      if (this.$refs.form.validate()) {     
        this.$store.dispatch("firestore/pacienteJoin", {
          pacienteInfos: this.pacienteInfos,
          pacienteNr: this.pacienteNr,
        });
        this.$store.dispatch("firestore/pacientePreAtendimento", {
          id: this.pacienteNr
        });           
      }
    },
    save(date) {
      this.$refs.menu.save(date);
    },
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    },
  },

  mounted() {
    // setTimeout(() => {
    //   this.createListWithNames();
    // }, 1000);
  },
};
</script>