                <v-card
                  v-bind="attrs"
                  v-on="on"
                  elevation="0"
                  color="grey lighten-2"
                >
                  <v-autocomplete
                    label="Nome do Paciente"
                    single-line
                    solo
                    class="mt-2 mx-3"
                    v-model="pacienteDocID"
                    :items="pacientes"
                    no-data-text="..."
                  ></v-autocomplete>
                </v-card>
              </template>
              <span
                >Selecione o nome de um paciente cadastrado para anexar a dieta
              </span>
            </v-tooltip>
          </v-col>
          <v-col cols="12" md="4">
            <v-btn class="mt-3">Ver histrico de atendimento</v-btn>
          </v-col>
