<script>
import firebase from "firebase";
import {
  mdiCalendar,
  mdiContentSave,
  mdiPrinter,
  mdiEye,
  mdiEmailSendOutline,
  mdiDownload,
  mdiMagnify,
} from "@mdi/js";

// import jspdf from "jspdf";
// import "jspdf-autotable";

// components imports
import FormulaFormEdit from "../components/home/formulas/FormulaFormEdit";
import router from "../router";

export default {
  components: { FormulaFormEdit },
  data: function () {
    return {
      pacienteDocID: "",
      snack: true,
      table: false,
      dialog: false,
      snackbar: true,
      // documentsList: [],
      icons: {
        mdiCalendar,
        mdiContentSave,
        mdiPrinter,
        mdiEye,
        mdiEmailSendOutline,
        mdiDownload,
        mdiMagnify,
      },
    };
  },
  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    formulas() {
      return this.$store.state.firestore.formulas;
    },
  },
  methods: {
    saveToFirestore: function () {
      firebase.firestore().collection("formulas").add(this.formula);
      // firebase.firestore().collection("users")
      //   .doc(sessionStorage.getItem("current-user"))
      //   .collection("formulas")
      //   .add(this.formula);

      setTimeout(() => {
        router.replace("/FormulasSalvas");
      }, 2000);
    },
  },
  mounted() {
    if (this.$store.state.firestore.formulas.length === 0) {
      this.$store.dispatch("firestore/getFormulas");
    }
    if (this.$store.state.firestore.pacientes.length === 0) {
      this.$store.dispatch("firestore/getPacientes");
    }
    // setTimeout(() => {
    //   this.createListWithNames();
    // }, 2000);
  },
};
</script>

