<template>
  <div>
    <v-form
      ref="form"
      v-model="valid"
    >
      <v-container v-if="userInfos !== null">
        <div class="d-flex justify-center">
          <v-avatar
            width="50%"
            height="50%"
            class="pr-2"
          >
            <!-- :src="userInfos.imagem" -->
            <img
              :src="userInfos.profilePicture"
              aspect-ratio="1"
              height=""
            />
          </v-avatar>
        </div>
        <v-file-input
          accept="image/*"
          label="Imagem de Perfil"
          :prepend-icon="icons.mdiImage"
          @change="handlePicture"
        ></v-file-input>
        <v-row class="d-flex justify-center">
          <v-col cols="12">
            <v-text-field
              solo
              :prepend-icon="icons.mdiAccount"
              v-model="userInfos.name"
              :rules="rules.requiredField"
              label="nome"
              required
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center">
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiShieldAccount"
              v-model="userInfos.cpf"
              :rules="rules.requiredField"
              label="CPF"
              required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiClipboardAccount"
              v-model="userInfos.crn"
              :rules="rules.requiredField"
              label="CRN"
              required
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="d-flex justify-center">
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiPhoneLock"
              v-model="userInfos.phone"
              :rules="rules.requiredField"
              label="Telefone para contato"
              required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiPhoneIncoming"
              v-model="userInfos.phoneClients"
              :rules="rules.requiredField"
              label="Telefone para clientes"
              required
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="d-flex justify-center">
          <v-col cols="12">
            <v-text-field
              solo
              :prepend-icon="icons.mdiDirections"
              v-model="userInfos.addressRoad"
              :rules="rules.requiredField"
              label="Endereo"
              required
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="d-flex justify-center">
          <v-col cols="12">
            <v-text-field
              solo
              :prepend-icon="icons.mdiMapMarker"
              v-model="userInfos.addressComplement"
              :rules="rules.requiredField"
              label="Complemento"
              required
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="d-flex justify-center">
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiCrosshairs"
              v-model="userInfos.addressCep"
              :rules="rules.requiredField"
              label="CEP"
              required
            ></v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiHomeCity"
              v-model="userInfos.addressBairro"
              :rules="rules.requiredField"
              label="Bairro"
              required
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="d-flex justify-center">
          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiMapSearchOutline"
              v-model="userInfos.addressCity"
              :rules="rules.requiredField"
              :label="userInfos.addressCity"
              required
            ></v-text-field>
          </v-col>

          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              solo
              :prepend-icon="icons.mdiFlag"
              v-model="userInfos.addressCountry"
              :rules="rules.requiredField"
              :label="userInfos.addressCountry"
              required
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-file-input
            accept="image/*"
            label="Logo empresarial"
            :prepend-icon="icons.mdiImage"
            @change="handleLogo"
          ></v-file-input>

          <v-switch
            class="px-10"
            v-model="useLogo"
            label="Usar a logo na dieta"
            color="success"
            hide-details
          ></v-switch>
        </v-row>

        <v-file-input
          accept="image/*"
          label="Assinatura Digital"
          :prepend-icon="icons.mdiImage"
          @change="handleAssinatura"
        ></v-file-input>
      </v-container>
      <v-container v-else></v-container>
    </v-form>
    <v-row align="center">
      <v-col
        class="text-center"
        cols="12"
      >
        <v-btn
          large
          color="#c5d13f"
          @click="submit"
        >Salvar</v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {
  mdiClipboardAccount,
  mdiShieldAccount,
  mdiPhoneLock,
  mdiPhoneIncoming,
  mdiDirections,
  mdiMapMarker,
  mdiHomeCity,
  mdiCrosshairs,
  mdiMapSearchOutline,
  mdiFlag,
  mdiImage,
  mdiAccount
} from "@mdi/js";

import firebase from "firebase";

export default {
  data: function () {
    return {
      userInfos: null,
      profilePicture: "",
      icons: {
        mdiClipboardAccount,
        mdiShieldAccount,
        mdiPhoneLock,
        mdiPhoneIncoming,
        mdiDirections,
        mdiMapMarker,
        mdiHomeCity,
        mdiCrosshairs,
        mdiMapSearchOutline,
        mdiFlag,
        mdiImage,
        mdiAccount
      },
      show: false,
      nome: "",
      valid: false,
      useLogo: false,
      pictureFile: null,
      assinaturaDigital: null,
      logo: null,
      rules: {
        requiredField: [(v) => !!v || "Campo obrigatrio"],
      },
    };
  },
  firestore () {
    return {
      userInfos: firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem("current-user")),
    };
  },
  computed: {
    alerts: function () {
      return this.$store.state.firestore.alerts;
    },
  },
  methods: {
    handlePicture (file) {
      this.pictureFile = file;
    },
    handleAssinatura (file) {
      this.assinaturaDigital = file;
    },
    handleLogo (file) {
      this.logo = file;
    },
    async pictureGet () {
      this.profilePicture = await firebase
        .storage()
        .ref("profileImages")
        .child(sessionStorage.getItem("current-user"))
        .getDownloadURL();
    },
    async submit () {
      if (this.$refs.form.validate()) {
        var infos = {};
        console.log(this.pictureFile);
        if (this.pictureFile !== null) {
          console.log("Enviando imagem!!");
          await firebase
            .storage()
            .ref("profileImages")
            .child(sessionStorage.getItem("current-user"))
            .put(this.pictureFile)
            .then(async (snapshot) => {
              infos.userPictureUrl = await snapshot.ref.getDownloadURL();
              console.log(infos.userPictureUrl);
            });
        }
        if (this.assinaturaDigital !== null) {
          console.log("Enviando assinatura");
          await firebase
            .storage()
            .ref("assinaturasDigitais")
            .child(sessionStorage.getItem("current-user") + "_assinatura")
            .put(this.assinaturaDigital)
            .then(async (snapshot) => {
              infos.userSignatureUrl = await snapshot.ref.getDownloadURL();
              console.log(infos.userSignatureUrl);
            });
        }
        if (this.logo !== null) {
          console.log("Enviando Logo");

          await firebase
            .storage()
            .ref("logos")
            .child(sessionStorage.getItem("current-user") + "_logo")
            .put(this.logo)
            .then(async (snapshot) => {
              infos.userLogoUrl = await snapshot.ref.getDownloadURL();
              console.log(infos.userLogoUrl);
            });
        }
        if (this.logo === null && this.userInfos.useLogo === true) {
          this.useLogo = false;
        }

        console.log(infos);
        console.log(this.useLogo)
        this.$store.dispatch("firestore/setUserPersonalData", {
          userInfos: this.userInfos,
          userEmail: firebase.auth().currentUser.email,
          userPictureUrl:
            infos.userPictureUrl == null ? "" : infos.userPictureUrl,
          userLogoUrl: infos.userLogoUrl == null ? "" : infos.userLogoUrl,
          userSignatureUrl:
            infos.userSignatureUrl == null ? "" : infos.userSignatureUrl,
          useLogo: this.useLogo,
        });
        this.$router.push("home");
      } else {
        console.log("Invalid data");
      }
    },
  },
  mounted () {
    // this.$store.dispatch()
    window.onbeforeunload = function () {
      return "";
    };
  },
};
</script>